html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-family: "Rubik", sans-serif;
  font-size: 1.6rem;
  color: hsl(270deg, 7%, 64%);
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
  overflow-x: hidden;
}
body::before {
  content: "";
  width: 37.4rem;
  height: 50rem;
  background: linear-gradient(to top, hsl(264deg, 100%, 61%), hsl(293deg, 100%, 63%));
  border-bottom-left-radius: 40rem;
  border-bottom-right-radius: 40rem;
  position: absolute;
  top: 0;
  left: -50%;
  z-index: -1;
  transition: all 0.4s;
  left: -40%;
}
@media (min-width: 480px) {
  body::before {
    width: 46.75rem;
  }
}
@media (min-width: 768px) {
  body::before {
    width: 56.6rem;
  }
}
@media (min-width: 992px) {
  body::before {
    width: 42.1rem;
    height: 70rem;
    left: -20%;
  }
}
@media (min-width: 1200px) {
  body::before {
    left: -5%;
    height: 72rem;
    width: 48rem;
  }
}
body::after {
  content: "";
  width: 37.4rem;
  height: 50rem;
  background-color: hsl(270deg, 20%, 96%);
  border-top-left-radius: 40rem;
  border-top-right-radius: 40rem;
  position: absolute;
  bottom: 0;
  right: -50%;
  z-index: -1;
  transition: all 0.4s;
}
@media (min-width: 480px) {
  body::after {
    width: 46.75rem;
  }
}
@media (min-width: 768px) {
  body::after {
    width: 56.6rem;
    right: -40%;
  }
}
@media (min-width: 992px) {
  body::after {
    width: 42.1rem;
    height: 70rem;
    right: -20%;
  }
}
@media (min-width: 1200px) {
  body::after {
    right: -12%;
    height: 72rem;
    width: 48rem;
  }
}

p {
  line-height: 1.7;
  margin: 0;
}

.contenedor {
  width: 90%;
  max-width: 1200px;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
  display: block;
}

h1, h2, h3 {
  margin: 0 0 2rem 0;
  font-family: "Rubik", sans-serif;
  color: hsl(271deg, 36%, 24%);
}

h1 {
  font-size: 4rem;
}

h2 {
  font-size: 1.8rem;
  margin: 0;
}

h3 {
  font-size: 2.8rem;
}

ul {
  padding: 0;
  list-style: none;
}

button {
  border: 0;
  background-color: transparent;
}
button:hover {
  cursor: pointer;
}

@keyframes updown {
  0% {
    transform: translateY(0rem);
  }
  50% {
    transform: translateY(-0.8rem);
  }
  100% {
    transform: translateY(0rem);
  }
}
.app__contenido {
  padding: 7rem 0;
  display: flex;
  flex-direction: column;
  gap: 5rem;
}
@media (min-width: 1400px) {
  .app__contenido {
    width: 75%;
    margin: 0 auto;
  }
}
@media (min-width: 768px) {
  .app__contenido {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(2, 1fr);
    align-items: center;
  }
}
@media (min-width: 992px) {
  .app__contenido {
    padding: 16rem 0;
  }
}
.app__pantalla {
  background-color: hsl(270deg, 20%, 96%);
  border: 10px solid #fff;
  border-radius: 4rem;
  position: relative;
  margin: 0 auto;
  max-width: 27rem;
  height: 54rem;
  box-shadow: 0 10px 60px 2px rgba(0, 0, 0, 0.3);
  font-size: 0.9rem;
  overflow: hidden;
}
.app__atras {
  width: 1.5rem;
  height: 1.5rem;
  background-image: url(../img/arrow.png);
  background-size: 1.5rem;
  background-repeat: no-repeat;
  background-position: center;
}
.app__puntos {
  display: flex;
  flex-direction: column;
  justify-content: center;
  gap: 0.2rem;
  border-radius: 50%;
  padding: 1rem 1.5rem;
  background-color: transparent;
  transition-property: background-color;
  transition-duration: 0.2s;
}
.app__puntos.activo {
  background-color: hsl(276deg, 100%, 81%);
}
.app__punto {
  width: 0.2rem;
  height: 0.2rem;
  background-color: #fff;
  border-radius: 50%;
}
.app__info {
  text-align: center;
}
@media (min-width: 768px) {
  .app__info {
    text-align: left;
  }
}
@media (min-width: 1200px) {
  .app__info {
    max-width: 45rem;
  }
}
.app__chat {
  padding: 1rem;
  background-color: hsl(270deg, 20%, 96%);
  height: 87%;
  border-radius: 0 0 3rem 3rem;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.personal {
  background: linear-gradient(90deg, hsl(264deg, 100%, 61%), hsl(293deg, 100%, 63%));
  display: flex;
  align-items: center;
  justify-content: space-between;
  border-top-left-radius: 3rem;
  border-top-right-radius: 3rem;
  border-bottom-left-radius: 0.5rem;
  border-bottom-right-radius: 0.5rem;
  padding: 2rem 1.5rem 0;
  position: relative;
  height: 13%;
}
.personal::before {
  content: "";
  background-color: #fff;
  width: 60%;
  height: 2rem;
  border-bottom-left-radius: 1.5rem;
  border-bottom-right-radius: 1.5rem;
  position: absolute;
  top: -0.1rem;
  left: 50%;
  transform: translateX(-50%);
}
.personal__contenido {
  display: flex;
  align-items: center;
  gap: 0.7rem;
}
.personal__info {
  display: flex;
  align-items: center;
  gap: 1rem;
}
.personal__imagen {
  width: 3rem;
  height: 3rem;
  border-radius: 50%;
  border: 2px solid #fff;
}
.personal__nombre {
  font-size: 1.4rem;
  color: #fff;
  font-weight: 700;
  line-height: 1.4;
}
.personal__estado {
  color: hsl(276deg, 100%, 81%);
  line-height: 1.4;
}

.p-conversacion {
  display: flex;
  flex-direction: column;
  gap: 0.8rem;
  width: 65%;
}
.p-conversacion__mensaje {
  background-color: #f3e0ff;
  color: hsl(276deg, 55%, 52%);
  border-radius: 1rem;
  padding: 0.5rem 1rem;
  border-bottom-left-radius: 0.3rem;
}

.s-conversacion {
  display: flex;
  flex-direction: column;
  gap: 0.8rem;
  align-items: flex-end;
  margin-left: auto;
}
.s-conversacion__imagen-c {
  display: flex;
  align-items: center;
  gap: 0.5rem;
}
.s-conversacion__imagen {
  width: 4.5rem;
  height: 4.5rem;
  border-radius: 1.5rem;
  animation-name: updown;
  animation-duration: 2s;
  animation-iteration-count: infinite;
}
.s-conversacion__mensaje {
  background-color: #fff;
  color: hsl(270deg, 7%, 64%);
  border-radius: 1rem;
  border-bottom-right-radius: 0.3rem;
  padding: 0.5rem 1rem;
}
.s-conversacion__mensaje:first-of-type {
  width: 76%;
}

.t-conversacion {
  display: flex;
  flex-direction: column;
  gap: 0.8rem;
}
.t-conversacion__mensaje {
  background-color: #f1dbff;
  color: hsl(276deg, 55%, 52%);
  width: 63%;
  border-radius: 1rem;
  padding: 0.5rem 1rem;
  border-bottom-left-radius: 0.3rem;
}

.seleccionar {
  display: flex;
  flex-direction: column;
  gap: 0.8rem;
  width: 77%;
}
.seleccionar__radio {
  display: flex;
  align-items: center;
  justify-content: space-between;
  background: linear-gradient(to right, hsl(293deg, 100%, 63%), hsl(264deg, 100%, 61%));
  border-radius: 1rem;
  padding: 0.4rem 0.8rem;
  border-bottom-left-radius: 0.3rem;
}
.seleccionar__radio-c {
  display: flex;
  align-items: center;
  gap: 1.5rem;
}
.seleccionar__input-radio {
  appearance: none;
  width: 1.5rem;
  height: 1.5rem;
  outline: 1px solid #fff;
  border-radius: 50%;
}
.seleccionar__input-radio:checked {
  border: 0.5rem solid hsl(271deg, 36%, 24%);
}
.seleccionar__texto {
  color: #fff;
}
.seleccionar__precio {
  font-size: 1.4rem;
  font-weight: bold;
  color: #fff;
}

.form-chat {
  position: relative;
  display: flex;
}
.form-chat__input {
  flex: 1;
  border-radius: 4rem;
  padding: 1.5rem;
  border: none;
  outline: 0;
}
.form-chat__input::placeholder {
  color: hsl(206deg, 6%, 79%);
}
.form-chat__submit {
  position: absolute;
  top: 50%;
  right: 1rem;
  background-color: hsl(271deg, 36%, 24%);
  border-radius: 50%;
  width: 3rem;
  height: 3rem;
  background-image: url(../img/arrow.png);
  background-size: 2rem;
  background-position: 0.4rem;
  background-repeat: no-repeat;
  transform: rotate(-180deg) translateY(50%);
}

.nav {
  position: absolute;
  top: 13%;
  right: 0;
  left: 0;
  bottom: 0;
  background-color: rgba(255, 255, 255, 0.7);
  backdrop-filter: blur(0.4rem);
  z-index: 10;
  border-bottom-left-radius: 3rem;
  border-bottom-right-radius: 3rem;
  flex-direction: column;
  gap: 5rem;
  padding: 3rem 0;
  align-items: center;
  display: none;
  opacity: 0;
}
.nav.activo {
  opacity: 1;
  display: flex;
}
.nav__enlace {
  color: #000;
  font-size: 2rem;
  font-style: italic;
  position: relative;
}
.nav__enlace::before {
  content: "";
  width: 100%;
  height: 0.2rem;
  background-color: hsl(289deg, 100%, 72%);
  position: absolute;
  bottom: 0;
  left: 0;
  transform: scaleX(0);
  transition-property: transform;
  transition-duration: 0.4s;
}
.nav__enlace:hover::before {
  transform: scaleX(1);
}

.autor {
  text-align: center;
}